
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
#nav {
  padding: 30px;
}
#nav a {
  font-weight: bold;
  color: #2c3e50;
  cursor: pointer;
}
#nav a:hover {
  text-decoration: underline;
}
#nav a.router-link-exact-active {
  color: #42b983;
}
#nav span.for-logout a {
  color: darkred;
}
#nav span.for-login a {
  color: blue;
}

.home {
  width: 75%;
  margin: 0 auto;
}
.home p {
  text-align: left;
}

form {
  width: 55%;
  margin: 0 auto;
}
#map {
  height: 450px;
}
#description {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 15px;
  font-weight: 300;
}
#infowindow-content .title {
  font-weight: bold;
}
#infowindow-content {
  display: none;
}
#map #infowindow-content {
  display: inline;
}
.pac-card {
  margin: 10px 10px 0 0;
  border-radius: 2px 0 0 2px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -moz-box-sizing: border-box;
  outline: none;
  -webkit-box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  background-color: #fff;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
}
#pac-container {
  padding-bottom: 12px;
  margin-right: 12px;
}
.pac-controls {
  display: inline-block;
  padding: 5px 11px;
}
.pac-controls label {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 13px;
  font-weight: 300;
}
#pac-input {
  background-color: #fff;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 15px;
  font-weight: 300;
  margin-left: 12px;
  padding: 0 11px 0 13px;
  text-overflow: ellipsis;
  width: 400px;
  height: 50px;
  left: 50% !important;
  margin-left: -200px;
  top: 10px !important;
  border: 4px solid rgba(0,0,0,.15) !important;
}
#pac-input:focus {
  border-color: #4d90fe;
}
#title {
  color: #fff;
  background-color: #4d90fe;
  font-size: 25px;
  font-weight: 500;
  padding: 6px 12px;
}
#target {
  width: 345px;
}

/* === Smartphones === */
@media only screen and (min-width : 320px) and (max-width : 767px) {
#pac-input {
    top: 65px !important;
    width: 350px !important;
    margin-left: -175px !important;
}
}

.login {
  width: 50%;
  margin: 0 auto;
}
.form-group label {
  text-align: left;
  display: block;
}

/* === Smartphones === */
@media only screen and (min-width : 320px) and (max-width : 767px) {
.login {
      width: 90% !important;
}
.btn-primary {
      width: 100% !important;
      height: 45px;
}
}




































